import React, { useState } from 'react'
import Modal from '../../components/modals/Modal'

const Home = () => {

  const [state, setstate] = useState(false);


  return (
    <div>
      <h4>Home Page</h4>
      <button onClick={() => setstate(true)}>open modal</button>
     {
       state &&
       <Modal
        closeModal ={()=> setstate(false)}
        
       />
    }
    </div>
  )
}

export default Home
